import Vue from 'vue'
import Vuex from 'vuex'
import http, {handlingXhrErrors} from './axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    users: [],
    devices: []
  },
  mutations: {
    setUsers (state, users) {
      if (Array.isArray(users) && users.length) {
        state.users = users
      }
    },
    setDevices (state, devices) {
      if (Array.isArray(devices) && devices.length) {
        state.devices = devices
      }
    }
  },
  actions: {
    listUsers (ctx, payload) {
      return new Promise((resolve, reject) => {
        http.get('/users')
          .then(res => {
            ctx.commit('setUsers', res.data)
            resolve(true)
          })
          .catch(err => { reject(handlingXhrErrors(err)) })
      })
    },
    listDevices (ctx, payload) {
      return new Promise((resolve, reject) => {
        http.get('/devices')
          .then(res => {
            ctx.commit('setDevices', res.data)
            resolve(true)
          })
          .catch(err => { reject(handlingXhrErrors(err)) })
      })
    },
    createUser (ctx, payload) {
      return new Promise((resolve, reject) => {
        http.post('/users', payload)
          .then(res => { resolve(res.data) })
          .catch(err => { reject(handlingXhrErrors(err)) })
      })
    },
    createDevice (ctx, payload) {
      return new Promise((resolve, reject) => {
        http.post('/devices', payload)
          .then(res => { resolve(res.data) })
          .catch(err => { reject(handlingXhrErrors(err)) })
      })
    },
    createSubscription (ctx, payload) {
      return new Promise((resolve, reject) => {
        http.post('/subscriptions', payload)
          .then(res => { resolve(res.data) })
          .catch(err => { reject(handlingXhrErrors(err)) })
      })
    }
  }
})
